.high_contrast_mode {
    margin-top: -20px;
    margin-left: 10px;
}

.high_contrast_mode_label {
    width: 65px;
    height: 30px;
    position: relative;
    display: block;
    background: #ebebeb;
    border-radius: 200px;
    box-shadow: inset 0px 5px 15px rgba(0, 0, 0, 0.4),
        inset 0px -5px 15px rgba(255, 255, 255, 0.4);
    cursor: pointer;
    transition: 0.3s;
}
.high_contrast_mode_label:after {
    content: "";
    width: 25px;
    height: 25px;
    position: absolute;
    top: 3px;
    left: 3px;
    background: linear-gradient(180deg, #ffcc89, #fffb21);
    border-radius: 180px;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
    transition: 0.3s;
}
.high_contrast_mode_input {
    width: 0;
    height: 0;
    visibility: hidden;
}
.high_contrast_mode_input:checked + .high_contrast_mode_label {
    background: #fff0a3;
}
.high_contrast_mode_input:checked + .high_contrast_mode_label:after {
    left: 62px;
    transform: translateX(-100%);
    background: linear-gradient(180deg, #777, #3a3a3a);
}
.high_contrast_mode_label:active:after {
    width: 30px;
}

